// URLÈ™åËØÅ
export const isValidUrl = (url) => {
  if (!url || typeof url !== 'string') return false
  
  try {
    new URL(url)
    return true
  } catch {
    // Â¶ÇÊûú‰∏çÊòØÂÆåÊï¥URLÔºåÊ£ÄÊü•ÊòØÂê¶ÂåÖÂê´Â∏∏ËßÅËßÜÈ¢ëÂπ≥Âè∞ÂüüÂêç
    const videoPatterns = [
      /douyin\.com/i,
      /dy\.com/i,
      /bilibili\.com/i,
      /b23\.tv/i,
      /youtube\.com/i,
      /youtu\.be/i
    ]
    
    return videoPatterns.some(pattern => pattern.test(url))
  }
}

// ËßÜÈ¢ëURLÈ™åËØÅ
export const isVideoUrl = (url) => {
  if (!isValidUrl(url)) return false
  
  const videoPatterns = [
    /douyin\.com/i,
    /dy\.com/i,
    /bilibili\.com/i,
    /b23\.tv/i,
    /youtube\.com/i,
    /youtu\.be/i,
    /tiktok\.com/i
  ]
  
  return videoPatterns.some(pattern => pattern.test(url))
}

// Ê£ÄÊµãËßÜÈ¢ëÂπ≥Âè∞
export const detectPlatform = (url) => {
  if (!url) return null
  
  const lowerUrl = url.toLowerCase()
  
  if (lowerUrl.includes('douyin.com') || lowerUrl.includes('dy.com')) {
    return 'douyin'
  }
  
  if (lowerUrl.includes('bilibili.com') || lowerUrl.includes('b23.tv')) {
    return 'bilibili'
  }
  
  if (lowerUrl.includes('youtube.com') || lowerUrl.includes('youtu.be')) {
    return 'youtube'
  }
  
  if (lowerUrl.includes('tiktok.com')) {
    return 'tiktok'
  }
  
  return null
}

// ÊèêÂèñÂàÜ‰∫´ÊñáÊú¨‰∏≠ÁöÑURL
export const extractUrlFromText = (text) => {
  if (!text) return null
  
  // ÂåπÈÖçURLÁöÑÊ≠£ÂàôË°®ËææÂºè
  const urlRegex = /(https?:\/\/[^\s]+)/g
  const matches = text.match(urlRegex)
  
  if (matches && matches.length > 0) {
    return matches[0].replace(/[.,;!?]$/, '') // ÁßªÈô§Êú´Â∞æÁöÑÊ†áÁÇπÁ¨¶Âè∑
  }
  
  return null
}

// È™åËØÅ‰ªªÂä°IDÊ†ºÂºè
export const isValidTaskId = (taskId) => {
  if (!taskId || typeof taskId !== 'string') return false
  
  // ‰ªªÂä°IDÂ∫îËØ•ÊòØÈùûÁ©∫Â≠óÁ¨¶‰∏≤ÔºåÈÄöÂ∏∏ÂåÖÂê´Â≠óÊØç„ÄÅÊï∞Â≠ó„ÄÅ‰∏ãÂàíÁ∫ø„ÄÅËøûÂ≠óÁ¨¶
  return /^[a-zA-Z0-9_-]+$/.test(taskId) && taskId.length > 0
} 